const baseHeader = {
	clientName: 'Mobile',
	clicentVersion: '3.0'
}
export default function fetchGet(url, params, header = {}) {
	if (params) {
		let paramsArray = []
		Object.keys(params).forEach(key => {
			paramsArray.push(`${key}=${encodeURIComponent(params[key]).toString()}`)
		})
		if (paramsArray.length > 0) {
			if (url.search(/\?/) === -1) {
				url += '?' + paramsArray.join('&')
			} else {
				url += '&' + paramsArray.join('&')
			}
		}
	}
	const request = fetch(url, {
		method: 'GET',
		headers: new Headers({
            'Accept': 'application/json',
            'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8',
            ...header,
            ...baseHeader
        })
	})
	return fetchResult(request)
}

/**
 * 处理网络请求结果
 * @param request
 * @returns {*}
 */
function fetchResult(request) {
    try {
        return request.then(response => {
            if (response.status === 200) {
                return response
            } else {
                throw response
            }
        }).catch(error => {
            if (error.json) {
                return error.json()
            } else {
                return Promise.reject('请求异常')
            }
        }).then(result => {
            if (result.status === 200) {
                let resultJson = result.json()
                return resultJson
            } else {
                if (result.exceptionInfo) {
                    if (result.exceptionInfo.length > 30) {
                        // eslint-disable-next-line
                      	throw '接口请求异常'
                    } else {
                        throw result.exceptionInfo
                    }
                } else {
                    throw result
                }
            }
        }).then(result => {
            return result
        })
    } catch (e) {
        return Promise.reject('请求异常')
    }
}