/**
 * 引导页 逻辑页面
 */
import React from 'react'
import { Icon, Button } from 'antd-mobile'
import '../style/guidance.scss'
import DownloadHoc from './hoc/download-hoc.js'
import { CSSTransition } from 'react-transition-group'

const DownButton = DownloadHoc(Button)

class Guidance extends React.Component{
	constructor(props) {
		super(props)
		this.state = {
			isHideBottom: false,
			isHideAlert: true,
			times: 1
		}
	}
	static defaultProps = {
		liveData: {}
	}
	hideBottom() {
		this.setState({
			isHideBottom: true
		})
	}
	hideAlert() {
		this.setState({
			isHideAlert: true,
			times: this.state.times + 1
		})
		if (this.state.times === 1) {
			let secondTimer = setInterval(() => {
				clearInterval(secondTimer)
				this.showAlert()
			}, 1000 * 60 * 5)
		}
	}
	showAlert() {
		this.setState({
			isHideAlert: false
		})
	}
	initTimer() {
		let firstTimer = setInterval(() => {
			clearInterval(firstTimer)
			this.showAlert()
		}, 1000 * 30)
	}
	componentDidMount() {
	}
	componentWillReceiveProps(nextProps) {
		if (this.props.match && this.props.match.path === '/detail/:id?') {
			if (nextProps.liveData.status !== 1) {
				this.initTimer()
			}
		} else {
			this.initTimer()
		}
	}
	render() {
		return (
			<div className='guidance'>
				<div className={['guidance-bottom', this.state.isHideBottom ? 'hide' : null].join(' ')}>
					<Icon onClick={this.hideBottom.bind(this)} className='guidance-bottom__close' type='cross'/>
					<div className="guidance-bottom__logo"></div>
					<div className='guidance-bottom__tips bg'>全民参与的体育直播</div>
					<DownButton className="guidance-bottom__download" type='primary'>立即下载</DownButton>
				</div>
				<CSSTransition
					in={!this.state.isHideAlert}
					timeout={300}
					unmountOnExit
					classNames='shade'
				>
					<div className="guidance-shade"></div>
				</CSSTransition>
				<CSSTransition
					in={!this.state.isHideAlert}
					timeout={300}
					unmountOnExit
					classNames='main'
				>
					<div className="guidance-alert">
						<div>
							<div className="guidance-alert__main">
								<div className="guidance-alert__main-text">用抓饭直播APP看直播</div>
								<div className="guidance-alert__main-text">众多好礼等着你</div>
								<div className="guidance-alert__main-logo bg"></div>
								<div className="guidance-alert__main-text">抓饭直播</div>
								<div className="guidance-alert__main-text">全民参与的体育直播</div>
								<DownButton type='primary' className="guidance-alert__main-button">下载客户端</DownButton>
							</div>
							<div className="guidance-alert__close">
								<Icon onClick={this.hideAlert.bind(this)} className="guidance-alert__close-icon" size='lg' type='cross-circle-o'/>
							</div>
						</div>
					</div>
				</CSSTransition>
			</div>
		)
	}
}

export default Guidance
// export default DownloadHoc(Guidance)
