/**
 * 图片组件
 * 预加载图片，加载成功之后显示，如果失败显示默认图片
 * props
 * url 必传
 * defaultUrl 可选
 */
import React from 'react'
import { CSSTransition } from 'react-transition-group'
import defaultUrl from '../images/default-header.png'
import '../style/animate.scss'

export default class ZfImg extends React.Component{
	constructor(props) {
		super(props)
		this.state = {
			defaultUrl: '',
			isShow: false
		}
	}
	loadImage(url) {
		let that = this
		let img = document.createElement('img')
		img.onload = function() {
			that.setState({
				defaultUrl: url,
				isShow: true
			})
		}
		img.onerror = function() {
			that.setState({
				defaultUrl: that.props.defaultUrl || defaultUrl,
				isShow: true
			})
		}
		img.src = url
	}
	componentDidMount() {
	}
	componentWillReceiveProps(nextProps) {
		if (!this.state.isShow) {
			this.loadImage(nextProps.url)
		}
	}
	componentDidUpdate() {
	}
	shouldComponentUpdate(nextProps) {
		if (this.state.isShow) {
			return false
		} else {
			return true
		}
	}
	render() {
		return (
			<CSSTransition
				in={this.state.isShow}
				timeout={300}
				classNames='image'
				unmountOnExit
			>
				<div className={this.props.className}
					style={{
						backgroundImage: `url('${this.state.defaultUrl}')`
					}}
				>
				</div>
			</CSSTransition>
		)
	}
}
