import React from 'react'
import { Button } from 'antd-mobile'
import '../style/nav-top.scss'
import DownloadHoc from './hoc/download-hoc.js'

const DownButton = DownloadHoc(Button)

class NavTop extends React.Component{
	constructor(props) {
		super(props)
		this.state = {}
	}
	render() {
		return (
			<div className='navTop'>
				<div className='navTop__logo bg'></div>
				<div className='navTop__button'>
					{
						!this.props.isHome ?
						<Button className='navTop__button-home' href='/' type='primary'>进入首页</Button> : null
					}
					<DownButton className='navTop__button-home open' type='primary'>打开APP</DownButton>
				</div>
			</div>
		)
	}
}

export default NavTop