import React from 'react'
import { Link } from 'react-router-dom'
import {
	delayFun,
	getElementToPageTop
} from '../common/utils.js'
import ZfImg from './ZfImg.js'

class VideoItem extends React.Component{
	constructor(props) {
		super(props)
		this.state = {
			top: 0,
			coverImage: null,
			screenHeight: window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight,
			t_top: 0,
			scrollHeight: 0
		}
		this.checkTop.bind(this)
	}
	static defaultProps = {
		big: false, // 默认false 背景高度
		isTop: false, // 是否需要向上移动margin-top
		scrollMain: window
	}
	isInView(target) {
		let s_top = 0
		if (this.props.scrollMain === window) {
			s_top = document.documentElement.scrollTop || document.body.scrollTop
		} else {
			s_top = this.props.scrollMain.scrollTop
		}
		this.setState({
			scrollHeight: s_top
		})
		return Math.abs(this.state.t_top - s_top) < this.state.screenHeight + 50
	}
	checkTop() {
		let target = this.state.coverImage
		let top = this.state.topImage
		if (target.style.backgroundImage) return
		if (!this.isInView(target)) return
		let src = target.getAttribute('data-src')
		let top_src = top.getAttribute('data-src')
		if (src) {
			target.style.backgroundImage = `url(${src})`
		} else if (top_src) {
			top.style.backgroundImage = `url(http://cover.justfun.fun${top_src})`
		}
	}
	delay() {
		let _self = this
		return delayFun(() => {
			_self.checkTop()
		}, 100)
	}
	componentWillUnmount() {
		this.props.scrollMain && this.props.scrollMain.removeEventListener('scroll', this.delay())
	}
	componentDidMount() {
		this.props.scrollMain && this.props.scrollMain.addEventListener('scroll', this.delay())
		this.setState({
			coverImage: this.refs.coverImage,
			topImage: this.refs.topImage,
			t_top: getElementToPageTop(this.refs.coverImage).y
		})
		if (this.props.isTop) {
			this.setState({
				top: this.refs.topImage.offsetWidth
			})
		}
		setTimeout(() => {
			this.checkTop()
		}, 0)
	}
	render() {
		let {data} = {...this.props}
		return (
			<div className='video-item' ref='videoItem'>
				<Link to={`/detail/${data.id}`} target="_blank">
					<div className={`video-item__top bg ${this.props.big ? 'big' : ''}`}
						data-src={data.screenshot}
						style={{
							// backgroundImage: `url(${data.screenshot ? 'http://cover.justfun.fun' + data.screenshot : '../images/default-live.png'})`,
							marginTop: `-${this.props.isTop ? this.state.top * 0.45 : 0}px`
						}}
						ref='topImage'
					>
						<div className='video-item__top-cover bg'
							ref='coverImage'
							data-src={data.cover}
						></div>
						<div className='video-item__top-shade'></div>
						<div className="video-item__top-bottom">
							<ZfImg
								className='video-item__top-bottom__header bg'
								url={data.figureUrl}
							/>
							<span className='video-item__top-bottom__name'>{data.nickname}</span>
							<span className='video-item__top-bottom__hint iconfont'>&#xe637;</span>
							<span className='video-item__top-bottom__number'>{data.hits}</span>
						</div>
					</div>
					<div className='video-item__bottom'>
						<div className='video-item__bottom-topic'>
							{
								data.topics && data.topics.length && (
									data.topics.map((item, index) => (
										<span key={index}>#{item}</span>
									))
								)
							}
						</div>
						<div className='video-item__bottom-name'>
							{data.name}
						</div>
					</div>
				</Link>
			</div>
		)
	}
}

export default VideoItem