import fetchGet from '../common/fetch.js'
/**
 * 获取首页推荐列表
 * @param  {[type]} params [description]
 * @return {[type]}        [description]
 */
export const getRecommendList = (params) => {
	return fetchGet('/proxy/ieg-hall/hall/index/recommend', params)
}

// 获取banner
export const getBannerList = (id) => {
	return fetchGet(`/proxy/ieg-config/configs/${id}`)
}

/**
 * 获取直播间基本信息
 */
export const getLivesData = id => {
	return fetchGet(`/proxy/ieg-live/lives/${id}`)
}
/**
 * 获取直播间分类
 */
export const getCategories = id => {
	return fetchGet(`/proxy/ieg-hall/hall/category/categories`)
}
/**
 * 获取指定分类下的直播间
 */
export const getClassifyLives = params => {
	return fetchGet('/proxy/ieg-hall/hall/rooms', params)
}