import React, {Component} from 'react'
import {
	awakeApp
} from '../../common/utils.js'

export default WrappedComponent => {
	class DownloadHoc extends Component{
		constructor(props) {
			super(props)
			this.state = {}
		}
		download() {
			awakeApp()
		}
		render() {
			return <WrappedComponent onClick={this.download.bind(this)} {...this.props}/>
		}
	}
	return DownloadHoc
}