export const hitsFilter = value => {
	if (!value) return ''
	value = parseInt(value, 10)
    if (value > 10000 && value < 100000000) {
      return (value / 10000).toFixed(1) + '万'
    } else if (value >= 100000000) {
      return (value / 100000000).toFixed(1) + '亿'
    } else {
      return value + ''
    }
}

export const delayFun = function (fn, interval) {
  let _self = fn
  let timer
  let firstTime = true
  return function () {
    let args = arguments
    let _me = this
    if (firstTime) {
      _self.apply(_me, args)
      firstTime = false
      return
    }
    if (timer) return
    timer = setTimeout(function () {
      clearTimeout(timer)
      timer = null
      _self.apply(_me, args)
    }, interval || 500)
  }
}

export const getElementToPageTop = (e) => {
  let x = 0
  let y = 0
  add(e)
  function add (e) {
    x += e.offsetLeft
    y += e.offsetTop
    if (e.offsetParent) {
      e = e.offsetParent
      add(e)
    }
  }
  return {'x': x, 'y': y}
}

export function awakeApp() {
    var b = navigator.userAgent.toLowerCase()
    var conf = {
        android: {
            url: 'http://file.justfun.fun/download/justfun_m_stand_release_3.0.0.apk',
            schema: 'justfun://android'
        },
        ios: {
            url: '',
            schema: ''
        }
    }
    var DEV = {
        _browser: {
            isAndroid: b.indexOf('android') !== -1,
            isWX: b.indexOf('micromessenger') !== -1,
            isIOS: b.indexOf('iphone') !== -1
        },
        openApp: function () {
            // 打开ios
            console.log(DEV._browser)
            var filter = DEV.filter()
            if (filter) {
                if (DEV._browser.isIOS) {
                    window.location = conf.ios.schema
                    setTimeout(function () {
                        window.location.href = conf.ios.url
                    }, 1000)
                } else {
                    // alert("android调用地址：" + conf.android.schema);
                    var e = document.createElement('iframe')
                    e.src = conf.android.schema
                    e.style.display = 'none'
                    document.body.appendChild(e)
                    setTimeout(function () {
                        window.location.href = conf.android.url
                        document.body.removeChild(e)
                    }, 1000)
                }
            } else {
                window.location.href = conf.android.url
            }
        },
        filter: function () {
            var ret = false
            if (DEV._browser.isAndroid || DEV._browser.isWX || DEV._browser.isIOS) {
                ret = true
            }
            return ret
        }
    }
    DEV.openApp()
}
