import React from 'react'
import VideoItem from './VideoItem.js'
import { WingBlank, Toast } from 'antd-mobile'
import {
	getRecommendList
} from '../api/index.js'
import NoneImage from '../images/nodata.png'
/**
 * 标题组件
 */
class RowTitle extends React.Component{
	constructor(props) {
		super(props)
		this.state = {}
	}
	render() {
		let {data} = {...this.props}
		return (
			<div className="title-wrapper">
				<span className='title-wrapper-icon bg'
					style={{backgroundImage: `url(${data.recommendIconUrl})`}}
				></span>
				<span className="title-wrapper-text"> {data.name} </span>
			</div>
		)
	}
}
/**
 * 列表组件
 */
class List extends React.Component{
	constructor(props) {
		super(props)
		this.state = {}
	}
	render() {
		let videoList = []
		for (let i = 0; i < 1; i++) {
			videoList = videoList.concat(this.props.row.roomList)
		}
		return (
			<div>
				<RowTitle data={this.props.row.category}></RowTitle>
				<div className='video-item-wrapper'>
					{
						videoList.map((item, index) => (
							<VideoItem
								data={item}
								key={index + 'item'}
								big={this.props.isShow || (this.props.isHot && videoList.length === 4 && (index === 1 || index === 2))}
								isTop={this.props.isHot && videoList.length === 4 && index === 2}
								{...this.props}
							></VideoItem>
						))
					}
				</div>
			</div>
		)
	}
}
class RecommandList extends React.Component{
	constructor(props) {
		super(props)
		this.state = {
			liveList: [],
			isLoading: true
		}
	}
	getRecomment() {
		Toast.loading('正在加载...', 0)
		getRecommendList({
			pageSize: 100,
			pageNum: 1
		}).then(res => {
			Toast.hide()
			this.setState({
				isLoading: false
			})
			let arr = []
			for (let i = 0; i < 1; i++) {
				arr = arr.concat(res.data)
			}
			this.setState({
				liveList: arr
			})
		})
	}
	setLiveList(list) {
		let _list = [{
			roomList: list,
			category: list[0].category
		}]
		this.setState({
			liveList: _list
		})
	}
	componentDidMount() {
		this.getRecomment()
	}
	render() {
		return (
			<WingBlank size='md' className={this.state.isLoading && 'recommand-min-height'}>
				{
					this.state.liveList.length || this.state.isLoading ?
					(this.state.liveList.map((item, index) => (
						<List
							row={item}
							key={index}
							isHot={item.category.name === '热门推荐'}
							isShow={item.category.id === 3}
							{...this.props}
						></List>
					))) : <img className='none-recommand' src={NoneImage}/>
				}
			</WingBlank>
		)
	}
}
export default RecommandList